import * as React from "react"; import { Link, graphql } from "gatsby"; import { MDXRenderer } from "gatsby-plugin-mdx"
import Layout from '@szcomps/sz/SzLayout'; import components from '@szcomps/sz/mdx'; import SzSeo from '@szcomps/sz/SzSeo'
//import Bio from '@szcomps/bio'; import Layout from '@szcomps/layout';

const MdxPostTemplate = ({ data, location }) => { // console.log("5 data="+JSON.stringify(data))    
  const post = data.mdx; const siteTitle = data.site.siteMetadata?.title || `Title`;  const { previous, next } = data;

return (<Layout location={location} title={siteTitle} szlink={''} ><SzSeo title={post.frontmatter.title} description={post.frontmatter.description || post.excerpt}  /> 
   <article className="blog-post" itemScope itemType="http://schema.org/Article" >
        <header><h1 itemProp="headline">asdasd{post.frontmatter.title}</h1><p>{post.frontmatter.date}</p></header>
            {/* <MDXProvider components={components}> */}
          <MDXRenderer>{post.body}</MDXRenderer><hr />
              {/* </MDXProvider> */}
        <footer>{/* <Bio /> */}</footer>
      </article>
    </Layout>  
              )
}
export default MdxPostTemplate; // {/*  */}
export const pageQuery = graphql`query MdxPostBySlug($id: String  $previousPostId: String $nextPostId: String) {  
    site { siteMetadata { title }    }
    mdx(          id: { eq: $id             }) { id excerpt(pruneLength: 160) body frontmatter { title  date(formatString: "MMMM DD, YYYY")  description   }  }
    previous: mdx(id: { eq: $previousPostId }) { slug frontmatter { title   }  }
    next: mdx(    id: { eq: $nextPostId     }) { slug frontmatter { title   }  }  }`;
// export const pageQuery = graphql` query {site {siteMetadata {title}}  }`;

