import React from "react";import Helmet from "react-helmet"; // import CssBaseline from '@mui/material/CssBaseline'; <CssBaseline />
import Container from '@mui/material/Container'; import SzSeo from './SzSeo';
import { useStaticQuery, graphql } from "gatsby"; import SzFooter from './Bfooter'; import SzHeader from './Bheader';

const Layout = ({ props, children, location, szlink}) => { // console.log("6 szL props.pageContext."+JSON.stringify(props)); 
  const data = useStaticQuery(graphql` query HeaderQuery { site { siteMetadata { submenu {title url} title tabTitle frontPageTitle footerHeading footerTitle vgua frontPageMaxWidth } } }`);
  const szdata=data.site.siteMetadata; // console.log(" 8d"+JSON.stringify(szdata.vgua))

return (
  <SzHeader location={location} sz2title={data.site.siteMetadata.frontPageTitle} szsubmenu={data.site.siteMetadata.submenu}  szlink={szlink} />

)} 

export default Layout; //    import { StaticQuery, graphql } from "gatsby";// data.site.siteMetadata.footerTitle 
