import React from "react"; import PropTypes from "prop-types"; import Helmet from "react-helmet"; import { useStaticQuery, graphql } from "gatsby"
// import szsiteconf from '@szass/constants/szsitemeta';
const szsiteconf={vgua:"qwe",description:"sdfsdf", title:"sdgsdg"}
function SzSeo({ props, description, lang, meta, keywords, title }) {// console.log("props.pageContext.timeToRead="+props.pageContext.timeToRead) social {twuser fbpage fbapp fbpixel } siteLanguage author {name summary}
  // const { site } = useStaticQuery(graphql`query {site {siteMetadata {  description  title } } } `); 
  // console.log("6 site="+JSON.stringify(site.siteMetadata))
  const szScr="window.dataLayer = window.dataLayer || []; function gtag(){dataLayer.push(arguments);}; gtag('js', new Date()); gtag('config', '"+szsiteconf.vgua+"');";
  const szggl='https://www.googletagmanager.com/gtag/js?id='; const szScrAs=szggl+szsiteconf.vgua;
  //
  const metaDescription = description || szsiteconf.description
/* sz2do  <meta name='twitter:card' content='summary'>{name:'twitter:title', content: title,},{name:'twitter:description', content: metaDescription,},
  <script type="text/javascript">{`window.dataLayer = window.dataLayer || []; function gtag(){dataLayer.push(arguments);};`}
      {`gtag("js", new Date()); gtag("config", "${szsiteconf.vgua}");`}                                        </script>

*/ 
  return (<Helmet><title itemProp="name" lang={lang}>{szsiteconf.title}</title>
  <meta name="description" content={metaDescription}                                   />
  <meta name="viewport" content="width=device-width,minimum-scale=1,initial-scale=1"   />
  
  <meta property='og:title' content={szsiteconf.title}                                 />
  <meta property="og:type" content='website'                                           />
  <meta property='og:description' content={metaDescription}                            />
  <base target="_blank" />
  <script src={szScrAs} type="text/javascript"                                         />
</Helmet> )}

SzSeo.defaultProps = {lang: 'en', meta: [], keywords: [],}
SzSeo.propTypes = {description: PropTypes.string, lang: PropTypes.string, meta: PropTypes.array, keywords: PropTypes.arrayOf(PropTypes.string), title: PropTypes.string.isRequired,}
export default SzSeo; //  sz2do      {name: 'twitter:creator', content: site.siteMetadata.author.name,},
